import { assert } from "@thi.ng/errors/assert";
/** @internal */
export const ensureSize = (data, width, height, stride = 1) => assert(data.length >= width * height * stride, "pixel buffer too small");
/** @internal */
export const ensureImageData = (data, width, height) => data
    ? (assert(data.width === width && data.height === height, "imagedata has wrong dimensions"),
        data)
    : new ImageData(width, height);
/** @internal */
export const ensureChannel = (fmt, id) => {
    const chan = fmt.channels[id];
    assert(chan != null, `invalid channel ID: ${id}`);
    return chan;
};
/** @internal */
export const ensureSingleChannel = (fmt) => assert(fmt.channels.length === 1, `require single channel buffer`);
/** @internal */
export const ensureAlpha = (fmt) => assert(!!fmt.alpha, "missing alpha channel");
