import { isNumber } from "@thi.ng/checks/is-number";
import { clamp } from "@thi.ng/math/interval";
export const __luminanceABGR = (c) => (((c >>> 16) & 0xff) * 29 + ((c >>> 8) & 0xff) * 150 + (c & 0xff) * 76) /
    255;
/** @internal */
export const __clampRegion = (sx, sy, w, h, maxw, maxh, dx = 0, dy = 0) => {
    sx |= 0;
    sy |= 0;
    w |= 0;
    h |= 0;
    sx < 0 && ((w += sx), (dx -= sx), (sx = 0));
    sy < 0 && ((h += sy), (dy -= sy), (sy = 0));
    return [sx, sy, clamp(w, 0, maxw - sx), clamp(h, 0, maxh - sy), dx, dy];
};
/** @internal */
export const __prepRegions = (src, dest, opts = {}) => {
    const sw = src.width;
    const dw = dest.width;
    let sx, sy;
    let dx, dy;
    let rw, rh;
    [sx, sy, rw, rh] = __clampRegion(opts.sx || 0, opts.sy || 0, opts.w || sw, opts.h || src.height, sw, src.height);
    [dx, dy, rw, rh, sx, sy] = __clampRegion(opts.dx || 0, opts.dy || 0, rw, rh, dw, dest.height, sx, sy);
    return { sx, sy, dx, dy, rw, rh };
};
/** @internal */
export const __setChannelUni = (dbuf, src, set) => {
    for (let i = dbuf.length; i-- > 0;) {
        dbuf[i] = set(dbuf[i], src);
    }
};
/** @internal */
export const __setChannelSame = (dbuf, sbuf, get, set) => {
    for (let i = dbuf.length; i-- > 0;) {
        dbuf[i] = set(dbuf[i], get(sbuf[i]));
    }
};
/** @internal */
export const __setChannelConvert = (dbuf, sbuf, from, sto, mask) => {
    const invMask = ~mask;
    for (let i = dbuf.length; i-- > 0;) {
        dbuf[i] = (dbuf[i] & invMask) | (from(sto(sbuf[i])) & mask);
    }
};
export const __transformABGR = (pix, format, fn) => {
    const from = format.fromABGR;
    const to = format.toABGR;
    for (let i = pix.length; i-- > 0;) {
        pix[i] = from(fn(to(pix[i])));
    }
};
/** @internal */
export const __asVec = (x) => isNumber(x) ? [x, x] : x;
/** @internal */
export const __asIntVec = (x) => {
    const v = __asVec(x);
    return [v[0] | 0, v[1] | 0];
};
